.wallet {
  background-color: #000000;
  position: absolute;
  display: none;
  top: 0;
  right: 0;
  padding: 20px;
  
}
.wallet pre {
  font-size: 15px;
  line-height: 200%;
  font-family: system-ui;
}

.solana {
  width: 22px;
  height: 22px;
  display: inline-block;
  position: relative;
  top: 4px;
}

.counter {
  text-align: center;
  color: #9972e7;
  display: flex;
  flex-direction: row;
  flex-flow: wrap;
  justify-content: center;
}
.counterunit {
  white-space: nowrap;
  font-size: max(18px, 5vw);
  color: #9972e7;
  margin-right: 10px;
}

.MuiList-root {
  background-color: #fff !important;
}
.MuiListItem-root {
  box-shadow: inset 0 1px 0 0 rgb(0 0 0) !important;
}
.MuiDialogTitle-root {
  color: #fff !important;
}

.MuiButton-label {
  justify-content: center !important;
}


/* wrappers */

* {
  background-size: contain;
  background-repeat: no-repeat;
  box-sizing: border-box;
  font-size: 18px;
}

.container {
  /* background-repeat: no-repeat; */
  background-repeat-y: repeat;
  background-color: #000000;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  overflow: auto;
  color: #fff;
  font-family: 'Montserrat', sans-serif;
}

.wrap {
  width: 100%;
  max-width: 1100px;
  margin: 0 auto;
}

.wrap-blk {
  background-color: #000;
}

.content {
  margin: 20px;
  display: flex;
  flex-direction: column;
  border-radius: 4px;
}

/* sections */

.hero {
  display: flex;
  flex-direction: row;
  padding: 20px;
  padding-bottom: 0;
  margin-top: 140px;
  
}

.cta .hl {
  font-size: 40px;
  font-weight: 700;
  text-shadow: 0 0 2px black;
  transform: scaleX(0.9);
    transform-origin: top left;
}
.cta .cp {
  text-shadow: 0 0 2px black;
}
.cp {
  margin-top: 50px;
}


.mint {
  /* background: #51348c; */
  background: #000000;
  
  color: #fff;
  margin: 10px 0px;
  border-radius: 4px;
  position: relative;
  padding: 30px;
  margin: 20px;
  text-align: center;
  padding-bottom: 0;
}
.mint.connected {
  background: #51348c;
}
.mint p {
  padding-left: 0;
}

.price {
  padding: 20px 0px;
  font-weight: 700;
  color: #fff;
}


.mintbtn {
  background-color: #fff !important;
  color: #000 !important;
  font-weight: 700 !important;
  padding: 6px 33px !important;
  box-shadow: 0 2px 0px 1px #000;
}
.mintbtn:hover {
  /* background-color: #f00 !important; */
  color: #000 !important;
  box-shadow: 0 0 0 1px #000;
}

.passivebtn {
  color: #000 !important;
  padding: 6px 33px !important;
  box-shadow: 0 0px 0px 2px #000;
  pointer-events: none;
}

.startdate {
  padding-top: 20px;
}

.nftinfo {
  text-shadow: 0 0 2px transparent;
  position: relative;
  top: 20px;
  font-size: 80%;
}


.lineup {
  width: 100vw;
  background-color: #000;
  background-position-x: center;
  background-position-y: 5px;
  background-size: cover;
}
.lineup img {
  opacity: 0;
}


.about{
  display: flex;
  justify-content: center;
  text-align: center;
  padding-top: 30px;
}
.aboutp {
  max-width: 80%;
  display: inline-block;
  text-align: center;
}


.seen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  border-radius: 20px;
  margin-top: 40px;
  background-color: #000;
  padding: 20px;
  /* padding-bottom: 40px; */
}

.details {
  display: flex;
  justify-content: center;
  text-align: center;
  /* border: 1px solid #51348c; */
  box-shadow: 0px 0px 0px 3px #51348c;
  border-radius: 20px;
}
.hl {
  font-size: 21px;
  font-weight: 700;
  padding-top: 20px;
}


.itemcontainer {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
  margin-top: 60px;
}

.item {
  width: 30%;
  background-color: #51348c;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
}

.itemimg {
  width: 100%;;
}

.itemc {
  width: 100%;
  text-align: center;
  padding: 20px;
}

.reflectionlineup {
  width: 100%;
  margin-bottom: 60px;
  margin-top: 60px;
}
.icon {
  width: 100%;
}

.item .ctx {
  flex: 1;
  min-height: 200px;
}

.footer {
  text-align: center;
  padding: 60px 0px;
  font-size: 80%;
}

/* elems */

strong { 
  display: block;
}

p {
  color: #eee;
  line-height: 1.65rem;
}

.spacer {
  padding: 20px 0;
  width: 100%;
}
.listinglinks {
  flex-direction: row;
  display: flex;
  justify-content: space-evenly;
  margin-top: 30px;
  margin-bottom: 30px;
}
.listinglinks a div {  
  width: 60px;
  height: 60px;
  margin: 10px;
  background-size: cover;
  background-position: center center;
}
.listinglinks a:link, .listinglinks a:visited {
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: normal;
}

.somelinks {
  flex-direction: row;
  display: flex;
  justify-content: left;
  margin-top: 30px;
}
.somelinks a div {
  border-radius: 100%;
  width: 60px;
  height: 60px;
  margin: 10px;
  background-size: cover;
  background-position: center center;
}

.details .somelinks {
  flex-direction: row;
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
.details .somelinks a div {
  margin: 30px;
}

.blocklink {
  margin-top: 10px;
  display: block;
}

a:link, a:visited {
  color: #000;
  text-decoration: none;
  font-weight: 600;
}
a:hover {
  color: purple;
  text-decoration: underline;
  
}


@media only screen and (max-width: 600px) {
  .container {
    background-repeat: repeat;
  }
  .content {
    margin: 0;
    margin-top: 0;
    border-bottom: none;
  }
  .hero {
    flex-direction: column;
    height: unset;
  }
  
  .wrap {
    width: 100%;
  }


  section p {
    padding: 0px 20px;
  }
  .item p {
    padding: 0;
  }

  .itemcontainer {
    flex-direction: column;
  }
  .item {
    width: 80%;
    height: unset;
    align-self: center;
    margin-bottom: 40px;
  }
  
  .itemc {
    top: 80vw;
    padding: 20px;
    text-align: center;
  }

  .seen {
    margin: 52px;
    margin-top: 10px;
  }
  .listinglinks {
    flex-direction: column;
    align-items: center;
  }
  .listinglinks a {
    margin-bottom: 40px;
  } 

  .listinglinks a div {  
    width: 80vw;
    height: 80vw;
    margin: 20px;
    background-size: cover;
    background-position: center center;
  }

  .details {
    margin: 50px;
  }
 
}

.overline {
  text-decoration: line-through;
}